@use "sass:math";
* {
  margin: 0;
  padding: 0;
}

body {
  font-family: "Noto Serif JP", serif;
  color: #383838;
}

input,
button,
select,
textarea {
  appearance: none;
  padding: 0;
  border: none;
  border-radius: 0;
  outline: none;
  background: transparent;
  font: inherit;
  -webkit-appearance: none;
  -moz-appearance: none;
}

input,
textarea,
select {
  border: 1px solid #E4E4E4;
  border-radius: 4px;
}

input[type=submit] {
  cursor: pointer;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

img {
  width: 100%;
}

.flex_center {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.kv {
  background-image: url(../img/kv.png);
  height: 900px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  padding: 0px 20px;
}

.kv_content {
  max-width: 1250px;
  margin: 0 auto;
  padding-top:60px;
  height: calc(100% - 60px - 80px);
  position: relative;
}

.kv__scroll {
  color: #fff;
  font-size: 12px;
  line-height: 8px;
  letter-spacing: 0.3em;
  font-weight: 400;
  white-space: nowrap;
  position: absolute;
  bottom: 243px;
  right: 8px;
  text-transform: uppercase;
  transform: translateX(100%) rotate(90deg);
  transform-origin: top left;
  padding-right: 174px;
  box-sizing: border-box;
  &::after {
    content: '';
    width: 154px;
    position: absolute;
    height: 1px;
    background: #fff;
    right: 0;
    top: 50%;
    transform: trasnlateY(-50%);
  }
}

.header_logo {
  max-width: 150px;
}

.nav ul li {
  margin: 0px 30px;
}

.nav  a {
  color: #FFF;
  font-family: 'Noto Serif JP', serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 40px;
  letter-spacing: 4.8px;
  position: relative;
}

.nav .flex_center a::after {
  position: absolute;
  left: 0;
  content: '';
  width: 100%;
  height: 2px;
  background: #FFF;
  bottom: -1px;
  opacity: 0;
}

.nav .flex_center a:hover::after {
  opacity: 1;
  transition: 0.3s;
}

.reservation_link {
  width: 140px;
  background-color: #B8A973;
  border-radius: 20px;
  // border: 1px solid #000;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
  text-align: center;
  margin-left: 20px;
}

.reservation_link:hover {
  opacity: 0.8;
  transition: 0.3s;
}

.kv_text {
  position: absolute;
  bottom: 0;
  left: 0;
  color: #FFF;
  font-family: 'Noto Serif JP', serif;
  font-style: normal;
  font-weight: 200;
  line-height: normal;
}

.kv_text p:first-child {
  font-size: 34px;
  letter-spacing: 10.2px;
  margin-bottom: 40px;
}

.kv_text p:last-child {
  font-size: 24px;
  letter-spacing: 7.2px;
  line-height: 40px;
}

.about {
  padding: 270px 20px 300px;
}

.about_content {
  max-width: 1250px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex: 1 auto;
}

.about p {
  color: #383838;
  font-family: 'Noto Serif JP',serif;
  font-size: 22px;
  font-style: normal;
  font-weight: 400;
  line-height: 46px;
  letter-spacing: 6.6px;
}

.about_text {
  width: 350px;
}

.about_logo {
  max-width: 260px;
  margin-bottom: 80px;
}

.about_images {
  position: relative;
  width: calc(100% - 350px);
  max-width: 735px;
  padding-top: 50%;
}


.about_images img:first-child {
  position: absolute;
  right: 0;
  top: 0;
  max-width: 610px;
  width: 83%;
}

.about_images img:last-child {
  position: absolute;
  left: 0;
  bottom: 0;
  max-width: 250px;
  width: 34%;
}

.br_sp {
  display: none;
}

.room {
  background-image: url(../img/room_bg.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  padding: 200px 20px;
  position: relative;
}

.room_content {
  max-width: 1200px;
  margin: 0 auto;
}

.content_title {
  max-width: 226px;
}

.ja_title {
