}

@media screen and (max-width: 1175px) {
  .plan .title {
      font-size: 18px;
  }
  .plan li {
      font-size: 11px;
  }
}

@media screen and (max-width: 1000px) {
  .form_content,.short_input {
      flex-flow: column;
      align-items: flex-start;
  }

  input,textarea,.short_input {
      max-width: 100%;
      width: calc(100% - 40px);
  }

  .short_input input,select {
      width: 100%;
      max-width: 100%;
  }

  .form p {
      width: 100%;
  }

  .reservation {
      padding: 52px 20px;
  }

  .address_content {
      width: 100%;
  }

  .form_select::after {
      top: 50%;
  }

  .form_text {
      margin-top: 60px;
  }

  .under_page_header .header {
      padding: 16px 20px;
  }

  .short_input_content {
      max-width: 100%;
  }

  .plan_contents {
      flex-direction: column;
  }

  .plan li {
      font-size: 14px;
  }

  .plan_cloud {
      right: 12px;
  }
}

@media screen and (max-width: 844px) {
  .kv {
      height: 380px;
  }
  .kv_content {
      padding-top: 16px;
      height: 355px;
  }
  .kv__scroll {
    display: none;
  }
  .kv_text {
      width: 100%;
  }
  .header_logo {
      width: 102px;
  }
  /* .nav {
      display: none;
  } */

  .br_sp {
      display: block;
  }

  .kv_text p:first-child {
      font-size: 24px;
      line-height: 36px;
      letter-spacing: 7.2px;
  }

  .kv_text p:last-child {
      font-size: 16px;
      line-height: 28px;
      letter-spacing: 5.4px;
  }

  .about {
      padding: 52px 20px;
  }

  .about_content {
      flex-flow: column;
  }
  .about_text {
      width: 100%;
      text-align: center;
  }
  .about_logo {
      max-width: 180px;
      margin-bottom: 40px;
  }
  .about p {
      font-size: 18px;
      line-height: 32px;
      letter-spacing: 5.4px;
      margin-bottom: 52px;
  }
  .about_images {
      width: 100%;
      height: auto;
      /* テクニックポイント（パディングハック） */
      padding-top: 75%;
  }
  .about_images img:first-child {
      width: 90%;
  }
  .about_images img:last-child {
      width: 40%;
  }

  .room,.plan {
      padding: 50px 20px;
  }

  .room_select_list {
      flex-flow: column;
      gap: 20px;
  }
  .room_cloud {
      width: 113px;
      top: 20px;
      right: 12px;
  }
  .room_description {
      margin: 50px 0px;
      font-size: 16px;
      line-height: 32px;
      letter-spacing: 4.8px;
  }
  .ja_title {
      font-size: 22px;
  }
  .en_title {
      font-size: 16px;
  }
  .content_title {
      max-width: 190px;
  }
  .line {
      height: 30px;
  }

  .room_textarea {
      padding: 20px;
  }

  .room_view .title {
      font-size: 20px;
      margin-bottom: 20px;
  }

  .room_view .text {
      font-size: 14px;
  }

  .thanks_text {
      padding: 0px 20px;
      padding-top: 52px;
      text-align: left;
  }

  .thanks_text p {
      text-align: left;
  }

  .thanks_text p:first-child {
      text-align: center;
  }

  .confirm_text table td {
      width: 100%;
      display: block;
  }

  .confirm_text table td:last-child{
      border-bottom: 1px solid #E4E4E4;
  }

  .confirm_text table td.no_underline {
      border-bottom: 0px solid #E4E4E4;
  }

  .confirm_form {
      flex-direction: column-reverse;
  }

  .back_submit {
      margin-top: 20px !important;
  }

  .confirm_text {
      padding-top: 52px;
  }

  .plan_cloud {
      width: 112px;
      top: 30px;
  }
}

@media screen and (max-width: 400px) {
  .plan_content {
      padding: 32px;
  }
}


.ui-datepicker {
background: #fff;
padding-top: 31px;
padding-bottom: 21px;
border: 1px solid #B8A973;
border-radius: 4px;
margin-top: -5px;
margin-left: 5px;
box-sizing: border-box;
position: absolute;
top: -9999px;
}

.ui-datepicker-header {
display: flex;
justify-content: center;
align-items: center;
width: 100%;
position: relative;
max-width: 280px;
margin: 0 auto 24px;
}

.ui-datepicker-title {
font-family: 'Noto Serif JP', serif;
font-size: 16px;
line-height: 12px;
letter-spacing: 0.3em;
padding-left: 0.3em;
}

.ui-datepicker-month {
margin-left: -0.4em;
}

.ui-datepicker-next,
.ui-datepicker-prev {
position: absolute;
top: 50%;
width: 12px;
height: 14px;
background: url(../img/next.png) no-repeat center / 12px auto;
font: 0 / 0 serif;
cursor: pointer;
}

.ui-datepicker-next {
right: 25px;
transform: translateY(-50%);
}

.ui-datepicker-prev {
left: 25px;
transform: translateY(-50%) scale(-1, -1);
}

.ui-datepicker-calendar{
border-spacing: 0;
color: #383838;
font-size: 16px;
letter-spacing: 0.3em;
font-weight: 400;
font-family: 'Noto Serif JP', serif;
}

.ui-datepicker-calendar thead th{
vertical-align: middle;
text-align: center;
width: 80px;
height: 44px;
font-weight: 400;
}

.ui-datepicker-calendar thead th:first-child {
color: #CC0000;
}

.ui-datepicker-calendar thead th:last-child {
color: #1F6FE8;
}

.ui-datepicker-calendar thead th span {
position: relative;
left: 0.15em;
}

.ui-datepicker-calendar tbody td{
vertical-align: middle;
text-align: center;
width: 80px;
height: 44px;
}

.ui-datepicker-calendar tbody td:hover {
background: #B8A973;
}

.ui-datepicker-calendar tbody td:hover a {
color: #fff;
}

.ui-datepicker-calendar tbody td a {
color: #383838;
font-size: 16px;
letter-spacing: 0.3em;
font-weight: 400;
font-family: 'Noto Serif JP', serif;
position:relative;
width: 100%;
height: 100%;
padding-left: 0.15em;
display: flex;
justify-content: center;
align-items: center;
width: 100%;
height: 100%;
}

.ui-datepicker-calendar tbody td.ui-datepicker-current-day{
background: #B8A973;
}

.ui-datepicker-calendar tbody td.ui-datepicker-current-day a{
color: #fff;
}

.hamburger_btn {
display: none;
}

.seasons {
background: #F9F4E2;
padding: 200px 0;
}
.seasons .content_title {
margin-bottom: 80px;
}

.seasons .ja_title {
margin-right: -0.3em;
}

.seasons_inner {
max-width: 1200px;
margin: 0 auto;
padding-right: 20px;
padding-left: 20px;
}

.seasons_txt {
font-size: 18px;
letter-spacing: 0.3em;
line-height: calc(34 / 18);
font-weight: 400;
font-family: 'Noto Serif JP',serif;
margin-bottom: 80px;
}

.seasons_pics {
margin: 0 auto 64px;
position: relative;
aspect-ratio: 1440 / 954;
width: 100%;
max-width: 1600px;
position: relative;
}

.seasons_pics .img1 {
font-size: 0;
aspect-ratio: 1020 / 536;
width: calc(1020% / 14.4);
position: absolute;
top: 0;
left: 0;
}

.seasons_pics .img1::before {
content: '';
z-index: 1;
width: calc(211% / 10.2);
aspect-ratio: 211 / 93;
position: absolute;
top: calc(110% / 5.36);
right: calc(-156% / 10.2);
background: url(../img/seasons-deco1.png) no-repeat center / 100%  100%;
}

.seasons_pics .img2 {
font-size: 0;
aspect-ratio: 230 / 300;
width: calc(230% / 14.4);
position: absolute;
right: calc(120% / 14.4);
top: calc(346% / 9.54);
}

.seasons_pics .img3 {
font-size: 0;
aspect-ratio: 586 / 338;
width: calc(586% / 14.4);
position: absolute;
left: calc(262% / 14.4);
bottom: 0;
}

.seasons_pics .img3::before {
content: '';
z-index: 1;
width: calc(155% / 5.86);
