.hamburger_btn {
  width: 38px;
  height: 38px;
  cursor: pointer;
  display: block;
  margin-left: 28px;
  position: relative;
  outline: none;
  background: transparent;
  border: none;
  box-sizing: border-box;
}
.hamburger_btn .bar {
  width: 20px;
  height: 1px;
  background: #fff;
  position: absolute;
  top: 12px;
  left: 50%;
  transform: translateX(-50%);
}
.hamburger_btn .txt {
  display: inline-block;
  padding-left: 0.3em;
  white-space: nowrap;
  color: #fff;
  font-size: 12px;
  letter-spacing: 0.3em;
  font-weight: 400;
  font-family: EB Garamond;
  position: absolute;
  bottom: 3px;
  left: 50%;
  transform: translateX(-50%);
  line-height: 8px;
}
.hamburger_btn .bar::before,
.hamburger_btn .bar::after {
  content: '';
  position: absolute;
  width: 20px;
  height: 1px;
  background: #fff;
  left: 50%;
  transform: translateX(-50%);
  transition: .3s ease;
  transition-property: transform, top;
}
.hamburger_btn .bar::before {
  top: -7px;
}
.hamburger_btn .bar::after {
  top: 7px;
}
.hamburger_btn:hover {
  opacity: 0.7;
}
.hamburger_btn.is-active .bar{
  background-color: transparent;
}
.hamburger_btn.is-active .bar::before {
  top: 0;
  transform: translateX(-50%) rotate(-45deg);
}
.hamburger_btn.is-active .bar::after {
  top: 0;
  transform: translateX(-50%) rotate(45deg);
}
